.a{
    height: 100vh;
    display: flex;
    align-items: center;
}
.a-left{
    flex:1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
}
.a-right{
    flex: 1;
}
.a-card.bg{
    position: absolute;
    top: 50px;
    left: 50px;
    background-color: #333;
}

.a-card{
    width: 60%;
    height: 70vh;
    border-radius:30px;
    position: relative;
    overflow: hidden;

}
.a-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.a-title {
    font-weight: 400;
  }
  
  .a-sub {
    margin: 20px 0px;
    margin-right: 30px;
    text-align: justify;
}
  
  .a-desc{
      font-weight: 300;
  }
  @media screen and (max-width: 480px) {
    .a {
        flex-direction: column;
        text-align: center;
        margin-top: 10px;
    }
    .a-left {
        width: 100%;
    }
    
    .a-card {
        height: 35vh;
    }

    .a-card.bg {
        display: none;
    }
    .a-right{
        padding:10px;
    }
    .a-sub {
        margin: 20px 0px;
        margin-right: 30px;
        text-align: center;
    }

}